* {
    margin: 0;
    padding: 0;
}

.app {
    max-width: 1280px;
    width: 90%;
    margin: 0 auto;
    min-width: 460px;
}

.app-header {
    position: relative;
}

.menu-button {
    position: absolute;
    width: 25px;
    height: 18px;
    display: flex;
    flex-flow: column;
    top: 15px;
    left: 30px;
    justify-content: space-between;
    transition: 1s;
}

.menu-button span {
    display: block;
    width: 25px;
    height: 4px;
    border-radius: 1px;
    background-color: #5A5A5A;
}

.app-name {
    color: #5A5A5A;
    font-size: 72px;
    padding: 25px;
    text-align: right;
}

.menu-area {
    position: absolute;
    left: -400px;
    width: 400px;
    height: 100%;
    min-height: 100vh;
    background-color: #5A5A5A;
    transition: 1s;
}

.menu-area.open {
    left: 0;
}

.menu-button span {
    transition: 0.3s;
}

.menu-button.open {
    left: 430px;
}

.menu-button.open span:nth-child(1) {
    transform: rotate(45deg) translate(0, 10px);
}

.menu-button.open span:nth-child(2) {
    display: none;
}

.menu-button.open span:nth-child(3) {
    transform: rotate(-45deg) translate(0, -10px);
}

.app-content {
    width: 80%;
    max-width: 1000px;
    margin: 0 auto;
    transition: 1s;
}

.app-content > div > div {
    display: grid;
    grid-template-columns: 0.8fr 1.2fr;
    grid-template-rows: repeat(4, auto);
    gap: 30px 30px;
}

.app-content > div > button {
    padding: 10px 60px;
    background-color: #31A96F;
    color: #FFFFFF;
    font-weight: bold;
    font-size: 48px;
    border: none;
    border-radius: 10px;
    margin: 0 auto;
    display: block;
    cursor: pointer;
}

.participants { grid-area: 1 / 1 / 2 / 2; }
.spending { grid-area: 1 / 2 / 2 / 3; }
.items { grid-area: 2 / 1 / 3 / 3; }
.results { grid-area: 3 / 1 / 4 / 3; }
.menu_open .participants { grid-area: 1 / 1 / 2 / 3; }
.menu_open .spending { grid-area: 2 / 1 / 3 / 3; }
.menu_open .items { grid-area: 3 / 1 / 4 / 3; }
.menu_open .results { grid-area: 4 / 1 / 5 / 3; }

.app-content.menu_open {
    margin: 0 auto 0 600px;
}

.box {
    border: 5px solid #31A96F;
    border-radius: 30px;
}

.box > p:last-child {
    font-size: 24px;
    color: #5a5a5a;
    font-weight: 700;
    text-align: right;
    padding: 10px 30px;
}

.box-header {
    box-sizing: border-box;
    width: calc(100% + 2px);
    position: relative;
    left: -1px;
    top: -2px;
    padding: 10px 40px;
    background-color: #31A96F;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px 20px 0 0;
}

.box-header h2 {
    font-size: 36px;
    color: #FFFFFF;
}

.box-header button {
    padding: 5px 10px;
    color: #31A96F;
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.box_item-box {
    width: calc(100% - 80px);
    margin: 10px auto;
    padding: 5px 30px;
    border: 1px solid #5a5a5a;
    border-radius: 5px;
    display: flex;
    align-items: center;
    position: relative;
}

.box_item-box p {
    margin-right: auto;
    transition: 0.5s;
}

.item-box_button {
    height: 30px;
    object-fit: contain;
    cursor: pointer;
    margin-right: 10px;
}

.item-box_button_approve {
    width: 30px;
    height: 30px;
    border: 2px solid #31A96F;
    border-radius: 3px;
    background-color: #fff;
    transition: 0.5s;
}

.box_item-box_approved .item-box_button_approve {
    background-color: #31A96F;
}

.box_item-box.box_item-box_approved {
    border: 1px solid #939292;
}

.box_item-box_approved p {
    color: #5a5a5a;
    text-decoration: line-through;
}

.shadow-bg {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    /*right: 0;*/
    /*bottom: 0;*/
    background-color: rgba(90, 90, 90, 0.2);
    z-index: 4;
}

.box_add-modal {
    position: absolute;
    display: none;
    width: 300px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 30px;
    flex-flow: column;
    align-items: center;
    background-color: #FFFFFF;
    border: 3px solid #31A96F;
    border-radius: 30px;
    z-index: 5;
}

.box_add-modal.open {
    display: flex;
}

.shadow-bg.open {
    display: block;
}

.box_add-modal input {
    margin: 15px 0;
    padding: 10px;
    width: 200px;
}

.box_add-modal button {
    margin-top: 20px;
    width: 50px;
    text-transform: uppercase;
    background-color: #31A96F;
    color: #FFFFFF;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    font-weight: bold;
}

.box_add-modal select {
    background-color: #FFFFFF;
    width: 200px;
    border: 1px solid #5a5a5a;
    border-radius: 3px;
    padding: 10px;
}


.item-box_participants-list {
    position: absolute;
    background-color: #fff;
    padding: 10px;
    width: 200px;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    font-size: 16px;
    right: 90px;
    top: 20px;
    display: none;
    z-index: 10;
    list-style-type: none;
}

.item-box_button_people:hover ~ .item-box_participants-list {
    display: block;
}

.item-box_participants-list:hover {
    display: block;
}

.item-box_participants-list li input {
    margin-right: 20px;
}

.item-box_participants-list li {
    margin: 10px 0;
    width: 100%;
}

.item-box_participants-list li label{
    display: block;
    width: 100%;
}

@media screen and (max-width: 1100px){
    .app {
        width: 95%;
    }
}

@media screen and (max-width: 950px){
    .participants { grid-area: 1 / 1 / 2 / 3; }
    .spending { grid-area: 2 / 1 / 3 / 3; }
    .items { grid-area: 3 / 1 / 4 / 3; }
    .results { grid-area: 4 / 1 / 5 / 3; }
}

@media screen and (max-width: 520px){
    .box-header h2 {
        font-size: 24px;
    }

    .box_item-box p {
        font-size: 14px;
    }
}


